.list{
    color:white;
    width:320px;
    background-color:lightcyan;
    height:50px;
    margin:20px auto;
    border-radius:5px;
}
.list p{
    padding:10px;
    text-align:left;
    position: relative;
}
.list p span{
    position: absolute;
    right:10px;
    cursor:pointer;
}
.faicons{
    margin:0 5px;
    font-size:15px;
    color:orangered;
}
.list p input{
    border:0;
    background-color:transparent;
    color: cornflowerblue;
    font-size:18px;
}
.list p input:focus{
    outline: none;
}