.todo-container{
    background-color:gray;
    width:400px;
    min-height:400px;
    margin:40px auto;
}
.todo-text{
    background-color: cornflowerblue;
    border:0;
    width:200px;
    height:50px;
    color:white;
    padding:0 20px;
    margin:20px;
    font-size:18px;
    border-radius:5px;
}
.todo-text:focus{
    outline: none;
}
.todo-button{
    font-weight: bold;
    width:80px;
    height:50px;
    border:0;
    color:white;
    background-color: orangered;
    border-radius:5px;
}